import { useState } from 'react'
import { FaPlus, FaMinus } from 'react-icons/fa'
import Accordion from './Accordion'
import Button from './Button'
import Faqs from './Faqs'
import Justice from './images/faqs.jpg'
import {useInView} from 'react-intersection-observer'
import {useEffect} from 'react'
import {useAnimation} from 'framer-motion'
import {motion} from 'framer-motion'


const Faq = ({id}) => {
  const [showAnswer, setShowAnswer] = useState(false)
  
  const toggle = (index) => {
    if(showAnswer === index){
      return setShowAnswer(null)
    }
    setShowAnswer(index)
  }
  
  
    const {ref, inView} = useInView({threshold: 0.2})
  const animation = useAnimation()
  const animation2 = useAnimation()
  
  useEffect(() => {
    if(inView){
      animation.start({
        y: 0,
        opacity: 1,
       transition: {type: 'spring', stiffness: 200} 
      })
    } 
   
     if(!inView){
      animation.start({
        opacity: 0,
        y: 50
       })
    }
  },[inView])
 
  
  useEffect(() => {
    if(inView){
      animation2.start({
        y: 0,
        opacity: 1,
       transition: {type: 'spring', stiffness: 200} 
      })
    } 
   
     if(!inView){
      animation2.start({
        opacity: 0,
        y: -100
       })
    }
  },[inView])
 
  return(
     <div ref={ref} className="FAQ">
      <h1> Frequently Asked Questions </h1>
      <div className ="container">
         <motion.div className="img-wrap"
         animate={animation} 
         >
          <img src={Justice} className="faq-image" alt="justice" />
         </motion.div>
         <motion.div className="accordion-container"
         animate={animation2} 
         >
             {Accordion.map((item, index) =>(
             <Faqs showAnswer={showAnswer} toggle={toggle} index={index} item={item} />
               ))} 
            <Button text="Ask More" />
         </motion.div>
      </div>
     </div>
    )
}

export default Faq