import { FaFacebook, FaTwitter, FaLinkedin } from 'react-icons/fa'
import A1 from './images/attorney-1.jpg' 
import A2 from './images/attorney-2.jpg' 
import A3 from './images/attorney-3.jpg' 
import A4 from './images/attorney-4.jpg' 
import A5 from './images/attorney-5.jpg' 
import {useInView} from 'react-intersection-observer'
import {useEffect} from 'react'
import {useAnimation} from 'framer-motion'
import {motion} from 'framer-motion'

const Attorneys = () => {
  
  const {ref, inView} = useInView({threshold: 0.1})
  const animation = useAnimation()
  const animation2 = useAnimation()
  
  useEffect(() => {
    if(inView){
      animation.start({
        x: 0,
        opacity: 1,
       transition: {type: 'spring', stiffness: 150} 
      })
    } 
   
     if(!inView){
      animation.start({
        opacity: 0,
        x: '-10vw'
       })
    }
  },[inView])
  
  
  useEffect(() => {
    if(inView){
      animation2.start({
        opacity: 1,
      })
    } 
   
     if(!inView){
      animation2.start({
        opacity: 0,
       })
    }
  },[inView])
 
  
  return(
      
      <motion.div ref={ref} className="attorneys"
      animate={animation2} 
      >
      <h1> Meet Our Expert Team Of attorneys </h1>
        <div id="attorney">
          <div className="attorney"
            animate= {animation} 
          >
            <div className="image">
              <img src={A1} alt="attorney" />
            </div>
            <div className="content">
              <div className="socials">
                <FaFacebook className="icon" />
                <FaTwitter className="icon" />
                <FaLinkedin className="icon" />
              </div>
              <h1>Aaliyah Sky</h1>
              <p>Family Consultant</p>
            </div>
           </div>
            
          <div className="attorney">
            <div className="image">
              <img src={A2} alt="attorney" />
            </div>
            <div className="content">
              <div className="socials">
                <FaFacebook className="icon" />
                <FaTwitter className="icon" />
                <FaLinkedin className="icon" />
              </div>
              <h1>Jane Wilson</h1>
              <p>Divorce Consultant</p>
            </div>
          </div>
            
          <div className="attorney">
            <div className="image">
              <img src={A3} alt="attorney" />
            </div>
            <div className="content">
              <div className="socials">
                <FaFacebook className="icon" />
                <FaTwitter className="icon" />
                <FaLinkedin className="icon" />
              </div>
              <h1>Alan Keen</h1>
              <p>Business Consultant</p>
            </div>
          </div>
            
          <div className="attorney">
            <div className="image">
              <img src={A4} alt="attorney" />
            </div>
            <div className="content">
              <div className="socials">
                <FaFacebook className="icon" />
                <FaTwitter className="icon" />
                <FaLinkedin className="icon" />
              </div>
              <h1>Frank Barnes</h1>
              <p>Immigration Consultant</p>
            </div>
          </div>
            
          <div className="attorney">
            <div className="image">
              <img src={A5} alt="attorney" />
            </div>
            <div className="content">
              <div className="socials">
                <FaFacebook className="icon" />
                <FaTwitter className="icon" />
                <FaLinkedin className="icon" />
              </div>
              <h1>Paul Keith</h1>
              <p>Criminal Consultant</p>
            </div>
        </div>
          
        
       
      </div>
      </motion.div>
    
    )
}

export default Attorneys 