import R1 from './images/review1.jpg' 
import R2 from './images/review2.jpg' 
import R3 from './images/review3.jpg' 
import {useInView} from 'react-intersection-observer'
import {useEffect} from 'react'
import {useAnimation} from 'framer-motion'
import {motion} from 'framer-motion'

const Review = () => {
  
  const {ref, inView} = useInView({threshold: 0.2})
  const animation = useAnimation()
  const animation2 = useAnimation()
  const animation3 = useAnimation()
  
  useEffect(() => {
    if(inView){
      animation.start({
        x: 0,
        opacity: 1,
       transition: {type: 'spring', stiffness: 200} 
      })
    } 
   
     if(!inView){
      animation.start({
        opacity: 0,
        x: -50
       })
    }
  },[inView])
  
  useEffect(() => {
    if(inView){
      animation2.start({
        y: 0,
        opacity: 1,
       transition: {type: 'spring', stiffness: 200} 
      })
    } 
   
     if(!inView){
      animation2.start({
        opacity: 0,
        y: -50
       })
    }
  },[inView])
  
  useEffect(() => {
    if(inView){
      animation3.start({
        x: 0,
        opacity: 1,
       transition: {type: 'spring', stiffness: 200} 
      })
    } 
   
     if(!inView){
      animation3.start({
        opacity: 0,
        x: 50
       })
    }
  },[inView])
  
  return(
     <div ref={ref} id="reviews">
      <h1> What Our Clients Are Saying </h1>
      <div className="reviews">
        <motion.div className="review"
        animate={animation} 
        >
          <div className="img-bg">
            <img src={R1} alt="review" />
          </div>
          <p> 
            Lorem ipsum dolor sit amet elit. Phasellus nec pretium mi. Curabitur facilisis ornare velit non vulputate. 
          </p>
          <h1> Marie Nate </h1>
        </motion.div>
        
        <motion.div className="review"
       animate={animation2} 
       >
          <div className="img-bg">
            <img src={R3} alt="review" />
          </div>
          <p> 
            Lorem ipsum dolor sit amet elit. Phasellus nec pretium mi. Curabitur facilisis ornare velit non vulputate. 
          </p>
          <h1> Fred Lam </h1>
        </motion.div>
        
        <motion.div className="review"
        animate={animation3} 
        >
          <div className="img-bg">
            <img src={R2} alt="review" />
          </div>
          <p> 
            Lorem ipsum dolor sit amet elit. Phasellus nec pretium mi. Curabitur facilisis ornare velit non vulputate. 
          </p>
          <h1 className="client-name"> Sussan March</h1>
        </motion.div>
      </div>
    </div>
    )
}

export default Review