import {useInView} from 'react-intersection-observer'
import {useEffect} from 'react'
import {useAnimation} from 'framer-motion'
import {motion} from 'framer-motion'
import {FaThumbsUp, FaGavel, FaCheck, FaLifeRing} from 'react-icons/fa'

const Qualities = () => {

  const {ref,inView} = useInView({threshold: 0.2})
  const animation = useAnimation()

  useEffect(() => {
    if (inView) {
      animation.start({
        opacity: 1,
        transition: {
          type: 'spring',
          stiffness: 200,
        }
      })
    }

    if (!inView) {
      animation.start({
        opacity: 0,
      })
    }
  },
    [inView])

  return(
    <motion.div ref={ref} className="qualities"
    animate ={animation} 
    >
      <div className="quality">
        <FaThumbsUp className="quality-icon" />
        <h1>Legal</h1>
        <p>Registered and Government Approved. </p>
     </div>
      <div className="quality">
        <FaGavel className="quality-icon" />
        <h1>Attorney</h1> 
        <p>The Best Attorneys in the country.</p>
      </div>
      <div className="quality">
        <FaCheck className="quality-icon" />
        <h1>Success</h1>
        <p>99% Success Rate</p>
      </div>
      <div className="quality">
        <FaLifeRing className="quality-icon" />
        <h1>Support</h1>
        <p>We offer the best client support.</p>
      </div>
    </motion.div>
    )
}

export default Qualities